import VueForm from '../../index.js'
import {
  VueTree
} from '../../index.js'

document.cookie = 'vueformstring="this is a string from cookie"; path=/'

export default {
  components: { VueForm, VueTree },
  data () {
    return {
      config1: {
        'cols': 2,
        'label-width': '100px',
        'values': {},
        'fields': [{
          'required': true,
          'type': 'input',
          'prop': 'name',
          'label': '',
          'options': {
            'placeholder': 'input your name'
          }
        }, {
          'type': 'radio',
          'prop': 'sex',
          'label': '',
          'options': {
            'radios': [{
              'text': '',
              'label': 'man'
            }, {
              'text': '',
              'label': 'woman'
            }]
          }
        }, {
          'type': 'checkbox',
          'prop': 'like',
          'label': '',
          'options': {
            'checkboxs': [{
              'text': '',
              'label': 'apple'  
            }, {
              'text': '',
              'label': 'orange'
            }]
          }
        }, {
          'type': 'select',
          'prop': 'company',
          'label': '',
          'options': {
            'options': [{
              'label': '',
              'value': 1
            }, {
              'label': '',
              'value': 2
            }, {
              'label': '',
              'value': 3
            }]
          }
        }, {
          'type': 'switch',
          'prop': 'status',
          'label': '',
          'options': {
            'active-text': '',
            'active-value': 1,
            'inactive-value': 0
          }
        }, {
          'type': 'tree',
          'prop': 'areacode',
          'label': '',
          'options': {
            'node-key': 'id',
            'show-checkbox': true,
            'value-type': 'leaf',
            'data': [{
              'id': 1,
              'label': '',
              'children': [{
                'id': 2,
                'label': ''
              }, {
                'id': 3,
                'label': ''
              }]
            }]
          }
        }, {
          'type': 'date',
          'prop': 'day',
          'label': '',
          'options': {}
        }, {
          'type': 'date',
          'prop': 'time',
          'label': '',
          'options': {
            'type': 'datetime'
          }
        }, {
          'type': 'upload',
          'prop': 'attach',
          'label': '',
          'col': 2,
          'options': {
            'action': 'your action',
            'tip': 'jpg/png',
            'list-type': 'text'
          }
        }, {
          'type': 'richtext',
          'prop': 'article',
          'col': 2
        }]
      },
      config2: {
        'values': {
          'name': 'this is default value'
        },
        'buttons': [{
          'text': '@submit',
          'icon': 'el-icon-info',
          'event': '@submit'
        }, {
          'text': 'clearFields',
          'icon': 'el-icon-success',
          'event': 'clearFields'
        }, {
          'text': '',
          'event': 'resetFields',
          'type': 'success'
        }],
        'fields': [{
          'type': 'input',
          'required': true,
          'prop': 'name'
        }]
      },
      values2: {},
      config3: {
        'cols': 3,
        'btns-position': 'right',
        'label-width': '80px',
        'fields': [{
          'type': 'input',
          'prop': 'input1',
          'label': 'input1',
          'col': 3
        }, {
          'type': 'input',
          'prop': 'input2',
          'label': 'input2',
          'col': 2
        }, {
          'type': 'input',
          'prop': 'input3',
          'label': 'input3',
          'col': 1
        }]
      },
      config4: {
        'buttons': [],
        'values': {
          'name': '#cookie.vueformstring'
        },
        'fields': [{
          'prop': 'name',
          'type': 'input'
        }]
      },
      values4: '', 
      treeOptions: {
        'node-key': 'id',
        'show-checkbox': true,
        'value-type': 'leaf',
        'data': [{
          'id': 1,
          'label': '',
          'children': [{
            'id': 2,
            'label': ''
          }, {
            'id': 3,
            'label': ''
          }]
        }]
      },
      treeValue: ''
    }
  },
  methods: {
    submit2 (values) {
      this.values2 = values
    },
    clear () {
      this.$refs['form'].clearFields()
    }
  }
}


